'use client';
import { useEffect, useState } from 'react';
import { createClient } from '@supabase/supabase-js';

const supabase = createClient(
  process.env.NEXT_PUBLIC_SUPABASE_URL!,
  process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!
);

interface Intel {
  id: number;
  region: string;
  headline: string;
  summary: string;
  sentiment_level: string;
  impact_score: number;
  confidence_score: number;
  insights: string;
  created_at: string;
}

export default function IntelFeed() {
  const [intel, setIntel] = useState<Intel[]>([]);
  const [status, setStatus] = useState<'offline' | 'listening' | 'synced'>('offline');

  useEffect(() => {
    setStatus('listening');

    // Initial load
    const fetchData = async () => {
      const { data } = await supabase
        .from('intel_feed')
        .select('*')
        .order('created_at', { ascending: false })
        .limit(10);
      setIntel(data || []);
    };
    fetchData();

    // Realtime listener
    const channel = supabase
      .channel('intel_feed_changes')
      .on(
        'postgres_changes',
        { event: 'INSERT', schema: 'public', table: 'intel_feed' },
        (payload) => {
          const newIntel = payload.new as Intel;
          setIntel((prev) => [newIntel, ...prev]);
          setStatus('synced');
          setTimeout(() => setStatus('listening'), 2000);
        }
      )
      .subscribe();

    return () => {
      supabase.removeChannel(channel);
    };
  }, []);

  return (
    <div className="p-4 rounded-2xl bg-slate-900 text-white w-full max-w-3xl mx-auto">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-lg font-bold">A.I.V.E. Live Feed</h2>
        <span
          className={`text-xs font-semibold px-2 py-1 rounded ${
            status === 'offline'
              ? 'bg-red-600'
              : status === 'listening'
              ? 'bg-yellow-500'
              : 'bg-green-600'
          }`}
        >
          {status.toUpperCase()}
        </span>
      </div>

      <div className="space-y-3 max-h-[500px] overflow-y-auto">
        {intel.map((item) => (
          <div
            key={item.id}
            className="p-3 bg-slate-800 rounded-xl border border-slate-700 hover:border-blue-500 transition-all"
          >
            <div className="flex justify-between items-center mb-1">
              <h3 className="font-semibold text-blue-300">{item.headline}</h3>
              <span
                className={`text-xs px-2 py-1 rounded ${
                  item.sentiment_level === 'Positive'
                    ? 'bg-green-600'
                    : item.sentiment_level === 'Negative'
                    ? 'bg-red-600'
                    : 'bg-gray-600'
                }`}
              >
                {item.sentiment_level}
              </span>
            </div>
            <p className="text-sm text-gray-300">{item.insights || item.summary}</p>
            <div className="flex justify-between items-center text-xs text-gray-400 mt-2">
              <span>üåç {item.region}</span>
              <span>üí° Confidence: {item.confidence_score}%</span>
              <span>üìä Impact: {item.impact_score}/100</span>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
