"use client";

import { useState, useEffect } from "react";
import { createClient } from "@supabase/supabase-js";

const supabase = createClient(
  process.env.NEXT_PUBLIC_SUPABASE_URL!,
  process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!
);

export default function SubmitIntel() {
  const [headline, setHeadline] = useState("");
  const [summary, setSummary] = useState("");
  const [region, setRegion] = useState("");
  const [sentiment, setSentiment] = useState("Neutral");
  const [impact, setImpact] = useState(50);
  const [loading, setLoading] = useState(false);
  const [recording, setRecording] = useState(false);
  const [analysis, setAnalysis] = useState<any>(null);
  const [timeline, setTimeline] = useState<number[]>([]);
  const [intelLog, setIntelLog] = useState<any[]>([]);
  const [mediaRecorder, setMediaRecorder] = useState<MediaRecorder | null>(null);

  // === Predictive timeline generator ===
  useEffect(() => {
    if (analysis?.confidence) {
      const base = analysis.confidence;
      const data = Array.from({ length: 9 }, (_, i) =>
        Math.max(10, base - i * (Math.random() * 8 + 4))
      );
      setTimeline(data);
    }
  }, [analysis]);

  // === Load last 5 intel from Supabase ===
  async function loadIntelLog() {
    const { data, error } = await supabase
      .from("intel_history")
      .select("*")
      .order("created_at", { ascending: false })
      .limit(5);
    if (!error && data) setIntelLog(data);
  }

  useEffect(() => {
    loadIntelLog();
  }, []);

  // === Handle voice recording ===
  const startRecording = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      const recorder = new MediaRecorder(stream);
      const chunks: any[] = [];
      recorder.ondataavailable = (e) => chunks.push(e.data);
      recorder.onstop = async () => {
        const blob = new Blob(chunks, { type: "audio/webm" });
        const formData = new FormData();
        formData.append("file", blob);
        formData.append("model", "whisper-1");

        const res = await fetch("https://api.openai.com/v1/audio/transcriptions", {
          method: "POST",
          headers: { Authorization: `Bearer ${process.env.NEXT_PUBLIC_OPENAI_KEY}` },
          body: formData,
        });

        const data = await res.json();
        if (data.text) setSummary(data.text);
      };
      recorder.start();
      setRecording(true);
      setMediaRecorder(recorder);
    } catch (err) {
      console.error("üé§ Voice record error:", err);
    }
  };

  const stopRecording = () => {
    if (mediaRecorder && recording) {
      mediaRecorder.stop();
      setRecording(false);
    }
  };

  // === Submit new intel to AIVE ===
  const handleSubmit = async (e: any) => {
    e.preventDefault();
    setLoading(true);

    try {
      const res = await fetch(process.env.NEXT_PUBLIC_AIVE_FUNCTION_URL!, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ headline, summary, region, sentiment, impact }),
      });

      const data = await res.json();
      setAnalysis(data);

      // Save to memory
      if (data.confidence) {
        const { error } = await supabase.from("intel_history").insert([
          {
            headline,
            summary,
            region,
            sentiment,
            impact,
            confidence: data.confidence,
          },
        ]);
        if (error) console.error("üß† Memory insert error:", error);
        else loadIntelLog(); // Refresh memory log
      }

      // Voice feedback
      if (data.insights) {
        const utter = new SpeechSynthesisUtterance(data.insights);
        utter.voice = speechSynthesis
          .getVoices()
          .find((v) => v.name.includes("Female"))!;
        utter.pitch = 1.1;
        utter.rate = 1.0;
        speechSynthesis.speak(utter);
      }
    } catch (err) {
      console.error("‚ùå A.I.V.E. analysis failed:", err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="w-full max-w-xl mx-auto bg-slate-900/80 backdrop-blur-lg rounded-2xl border border-slate-800 p-6 shadow-lg mt-6">
      <h2 className="text-lg font-semibold text-slate-200 mb-3 text-center">
        Submit Intel to <span className="text-amber-400">A.I.V.E.</span>
      </h2>

      <form onSubmit={handleSubmit} className="flex flex-col gap-3">
        <input
          type="text"
          placeholder="Headline"
          value={headline}
          onChange={(e) => setHeadline(e.target.value)}
          required
          className="bg-slate-800 text-slate-100 p-2 rounded-md outline-none border border-slate-700 focus:border-amber-400"
        />

        <div className="relative">
          <textarea
            placeholder="Summary or Description"
            value={summary}
            onChange={(e) => setSummary(e.target.value)}
            required
            rows={4}
            className="bg-slate-800 text-slate-100 p-2 rounded-md w-full outline-none border border-slate-700 focus:border-amber-400"
          />
          <button
            type="button"
            onClick={recording ? stopRecording : startRecording}
            className={`absolute right-3 bottom-3 text-xs px-2 py-1 rounded-md ${
              recording
                ? "bg-red-500 text-white animate-pulse"
                : "bg-amber-400 text-slate-900 hover:bg-amber-300"
            }`}
          >
            {recording ? "Stop üéôÔ∏è" : "Record Voice"}
          </button>
        </div>

        <input
          type="text"
          placeholder="Region (e.g., US, EU, Asia)"
          value={region}
          onChange={(e) => setRegion(e.target.value)}
          className="bg-slate-800 text-slate-100 p-2 rounded-md outline-none border border-slate-700 focus:border-amber-400"
        />

        <div className="flex items-center justify-between">
          <label className="text-slate-400 text-sm">Sentiment:</label>
          <select
            value={sentiment}
            onChange={(e) => setSentiment(e.target.value)}
            className="bg-slate-800 text-slate-100 px-3 py-1 rounded-md border border-slate-700 focus:border-amber-400"
          >
            <option>Positive</option>
            <option>Neutral</option>
            <option>Negative</option>
          </select>
        </div>

        <div className="flex items-center justify-between">
          <label className="text-slate-400 text-sm">Impact: {impact}</label>
          <input
            type="range"
            min={0}
            max={100}
            value={impact}
            onChange={(e) => setImpact(parseInt(e.target.value))}
            className="w-3/5 accent-amber-400"
          />
        </div>

        <button
          type="submit"
          disabled={loading}
          className="bg-gradient-to-r from-amber-500 to-yellow-400 text-slate-900 font-semibold py-2 rounded-md hover:scale-[1.02] transition-all mt-3 disabled:opacity-60"
        >
          {loading ? "Analyzing..." : "Submit Intel"}
        </button>
      </form>

      {/* === A.I.V.E. Analysis Output === */}
      {analysis && (
        <div className="mt-6 text-slate-100 text-sm bg-slate-800/60 p-4 rounded-md border border-slate-700">
          <p>
            <span className="text-amber-400 font-semibold">Confidence:</span>{" "}
            {analysis.confidence ?? "N/A"}%
          </p>
          <p>
            <span className="text-amber-400 font-semibold">Sentiment:</span>{" "}
            {analysis.overallSentiment ?? sentiment}
          </p>
          <p className="mt-2 text-slate-300 italic">
            {analysis.insights ?? "..."}
          </p>
        </div>
      )}

      {/* === Predictive Awareness Timeline === */}
      {timeline.length > 0 && (
        <div className="flex items-center justify-center gap-2 mt-6">
          {timeline.map((confidence, i) => {
            const color =
              confidence > 75
                ? "bg-green-400"
                : confidence > 50
                ? "bg-yellow-400"
                : "bg-red-400";
            return (
              <div
                key={i}
                className={`${color} w-6 h-6 rounded-full opacity-80 hover:opacity-100 transition`}
                title={`Day ${i * 10}: ${confidence.toFixed(0)}% confidence`}
              />
            );
          })}
        </div>
      )}

      {/* === Memory Log / Recent Intelligence === */}
      {intelLog.length > 0 && (
        <div className="mt-8 bg-slate-800/60 rounded-lg border border-slate-700 p-4">
          <h3 className="text-sm uppercase text-amber-400 font-semibold mb-2">
            Recent Intelligence Log
          </h3>
          <ul className="space-y-2 text-sm text-slate-300">
            {intelLog.map((item) => (
              <li
                key={item.id}
                className="border-b border-slate-700 pb-2 last:border-none"
              >
                <div className="flex justify-between">
                  <span className="font-semibold text-slate-100">
                    {item.headline}
                  </span>
                  <span className="text-xs text-slate-400">
                    {new Date(item.created_at).toLocaleString()}
                  </span>
                </div>
                <div className="text-xs text-slate-400">
                  {item.region} ‚Ä¢ {item.sentiment} ‚Ä¢ {item.confidence}%
                </div>
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
}
